@import url(https://fonts.googleapis.com/css2?family=Cinzel&family=Montserrat:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Krona+One&display=swap);
body {
   
  background-image: url("https://image.freepik.com/vector-gratis/fondo-blanco-gris-brillante-lineas-onduladas_1017-25101.jpg");
  height: 100%;
 
  background-repeat: no-repeat;
  background-size: cover;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo {
    width: 50px;
}
.nav-link {
    display: flex;
    align-items: center;
    font-size: x-large;
}
.avatar {
    width: 50px;
}
.tags {
    width: 100px;
}
.post-card{
    margin: auto;
}

.icon {
    text-align: right;
}
.user-card {

      font-family:'Montserrat', sans-serif;
      width: 16rem;
      text-decoration: none;
    margin: auto;
        margin-bottom: 50px;



}
.card-title {
    color: black;
    font-weight: bolder;
}

.avatarlist  {

    height: 180px;
    width: 180px;
    object-fit: cover;

}
.userCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    text-align: center;
}
.directory{
    text-align: center;
    margin: 100px;
    font-size: 3em;
          font-family:'Montserrat', sans-serif;
          text-transform: uppercase;

}

.profile {
    font-family:'Montserrat', sans-serif;
    margin-top: 50px;
    margin-bottom: 50px;
}

h1, h2, h3, h4, h5 {
    font-weight: bolder;
}
.links {
    display: flex;
    flex-direction: column;
   
}
.logos {
    width: 50px;
}
.warName {

    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-around;
}

.profilePicture{
    width: 300px;
    margin-bottom: 20px;
}
.profile p {
    margin-top: 40px;
    font-size: 1.3em;
}
main {
      font-family:'Montserrat', sans-serif;
}

.form-label {
      font-weight: bolder;
      font-size: 1.2em;

}
.signup {
    margin-top: 10%;
}
.login {
  font-family:'Montserrat', sans-serif;
}
#logo {
  width: 150px;
}

.principal {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ironhacker{
  margin-top: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;

}
.title{
  font-family:'Montserrat', sans-serif;
  text-transform: uppercase;
  
}
.slogan {
  font-family: 'Cinzel', serif;
  text-transform: uppercase;
}
